.LoadingTodo-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    /* border-radius: 10px; */
    padding: 24px 0;
  }
  
  .LoadingTodo-text {
    margin: 4px 0 4px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }

  .LoadingTodo-text p{
    max-width: 500px;
    width: 40%;
    min-width: 200px;
    height: 16px;
    margin: 0;
  }
  
  .LoadingTodo-completeIcon,
  .LoadingTodo-deleteIcon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 26px;
    width: 26px;
  }
  .LoadingTodo-completeIcon {
    position: absolute;
    left: 24px;
    border-radius: 50px;
  }
  .LoadingTodo-deleteIcon {
    position: absolute;
    right: 24px;
  }
  
  .LoadingTodo-text p,
  .LoadingTodo-container,
  .LoadingTodo-completeIcon,
  .LoadingTodo-deleteIcon {
    background: linear-gradient(90deg, rgba(250,250,250,1), var(--color-primary));
    background-size: 400% 400%;
    animation: loadingAnimation 3s ease-in-out infinite;
  }

  .LoadingTodo-container{
    background: linear-gradient(90deg, var(--background-color2), var(--background-color2));

  }
  
  @keyframes loadingAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }